import React, { useState, useEffect } from "react";
import "../components/background.css";
import { Questionaire } from "../components";

const API_URL =
  "https://opentdb.com/api.php?amount=5&category=15&difficulty=easy&type=multiple";

function App() {
  const [questions, setQuestions] = useState([]);
  const [currentIndex, setCurrentIndex] = useState(0);
  const [score, setScore] = useState(0);
  const [showAnswers, setShowAnswers] = useState(false);
  const [clicked, setClicked] = useState("");

  useEffect(() => {
    fetch(API_URL)
      .then((res) => res.json())
      .then((data) => {
        const questions = data.results.map((question) => ({
          ...question,
          answers: [
            question.correct_answer,
            ...question.incorrect_answers,
          ].sort(() => Math.random() - 0.5),
        }));
        setQuestions(questions);
      });
  }, []);

  const handleAnswer = (answer) => {
    if (!showAnswers) {
      if (answer === questions[currentIndex].correct_answer) {
        setScore(score + 1);
      }
      setClicked(answer);
    }

    setShowAnswers(true);
  };

  const handleNextQuestion = () => {
    setShowAnswers(false);

    setCurrentIndex(currentIndex + 1);
  };

  function refreshPage() {
    window.location.reload(false);
  }

  return questions.length > 0 ? (
    <div>
      {currentIndex >= questions.length ? (
        <div className="flex-body">
          <div className="gameEnd">
            Your score is: <br></br>
            {score} out of {questions.length}.
          </div>
          <div onClick={refreshPage} className="playAgain unselectable">
            Play Again
          </div>
          <footer>Krasimir Trendafiloff • 2021</footer>
        </div>
      ) : (
        <Questionaire
          data={questions[currentIndex]}
          showAnswers={showAnswers}
          handleAnswer={handleAnswer}
          handleNextQuestion={handleNextQuestion}
          clicked={clicked}
        />
      )}
    </div>
  ) : (
    <div className="body">
      <header className="unselectable">Loading</header>
      <div className="loader"></div>
    </div>
  );
}

export default App;
