import React from "react";

const Questionaire = ({
  answer,
  clicked,
  showAnswers,
  handleAnswer,
  handleNextQuestion,
  data: { question, correct_answer, answers },
}) => {
  return (
    <div className="flex-body">
      <header className="unselectable">Quiz</header>
      <div className="middle">
        <div>
          <div className="container">
            <div
              className="quest"
              dangerouslySetInnerHTML={{ __html: question }}
            ></div>
            <div className="answersContainer unselectable">
              {answers.map((answer, idx) => {
                const idColor = showAnswers
                  ? answer === clicked
                    ? "clicked "
                    : " "
                  : " ";

                const wcolor = showAnswers
                  ? answer === correct_answer
                    ? "correct "
                    : "random "
                  : " ";

                return (
                  <div
                    key={idx}
                    className={`${idColor}${wcolor}answer`}
                    onClick={() => handleAnswer(answer)}
                    dangerouslySetInnerHTML={{ __html: answer }}
                  ></div>
                );
              })}
              {showAnswers && (
                <div onClick={handleNextQuestion} className="next">
                  Next Question
                </div>
              )}
            </div>
          </div>
        </div>
      </div>
      <footer>Krasimir Trendafiloff • 2021</footer>
    </div>
  );
};

export default Questionaire;
